	import divider from '@/components/divider.vue';
	
	//引入网络请求
	import $H from '@/common/request.js';
	import $C from '@/common/config.js';
	import $U from '@/common/util.js';
	
	export default {
		components: {
			divider
		},
		data() {
			return {
				background: {
					backgroundColor: '#fff',
				},
				OrderCount: 0,
				OrderCountOneDay: 0,
				merchantVisitRecordCount: 0,
				merchantVisitRecordCountOneDay: 0,
				sumPayPrice: 0,
				sumPayPriceOneDay: 0,
				open: false,
				dataList:[]
			}
		},

        onShow(data) {
        	if(data){
				if(data=="true"){
					this.open=true
				}
				else{
					this.open=false
				}
			}
        },
        
		onLoad() {
			this.getData()
			this.getList()
		},


		methods: {
			
			getData(){
				// 发送到服务端
				$H.get("/merchant/statistics/home").then((res) => {
					//请求成功
					this.OrderCount=res.data.OrderCount
					this.OrderCountOneDay=res.data.OrderCountOneDay
					this.merchantVisitRecordCount=res.data.merchantVisitRecordCount
					this.merchantVisitRecordCountOneDay=res.data.merchantVisitRecordCountOneDay
					this.sumPayPrice=res.data.sumPayPrice
					this.sumPayPriceOneDay=res.data.sumPayPriceOneDay
					
				}).catch((e) => {
					
					//请求失败
					console.log("失败"+e)
				})
			},
			
			getList(){
				let data={
					type:2,
					size:3,
					current:1
				}
				// 发送到服务端
				$H.get("/order/page/all",data).then((res) => {
					//请求成功
					this.dataList = res.data.records;
					
					
				}).catch((e) => {
					
					//请求失败
					console.log("失败"+e)
				})
			},
			
			toMoreGoods() {
				uni.navigateTo({
					url: '../moreGoods/moreGoods'
				})
			},

			// 开启商城系统
			openShop() {
				uni.showModal({
					title: "提示",
					content: "是否要开启商城系统",
					confirmColor: "#FF7200",
					success: (res) => {
						if (res.confirm) {
							this.open = true
						}
					}
				})

			},

			// 设置商城
			shopSetting() {
				uni.navigateTo({
					url: '../shopSetting/shopSetting?open=' + this.open
				})
			},
			
			toOrder(){
				uni.navigateTo({
					url: '../moreGoods/moreGoods'
				})
			},
			
			toGoods(){
				uni.navigateTo({
					url: '../goodsControl/goodsControl'
				})
			},
			
			toOrderDetail(){
				uni.navigateTo({
					url:'../orderDetail/orderDetail'
				})
			},
			
			toCreateGoods(){
				uni.navigateTo({
					url:'../createGoods/createGoods'
				})
			}
		}
	}
